.counter_container {
    margin: 20px;
    padding: 10px;
    min-height: 230px;
    min-width: 290px;
    box-sizing: border-box;
    border: 2px solid aquamarine;
    border-radius: 8px;
}

.btn_container {
    height: 60px;
    width: 100%;
    border: 2px solid aquamarine;
    box-sizing: border-box;
    border-radius: 8px;
    display: flex;
    justify-content: center;
    align-items: center;
}