.value_container {
    margin-bottom: 20px;
    padding: 5px;
    box-sizing: border-box;
    height: 120px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: aquamarine;
    border: 2px aquamarine solid;
    border-radius: 3px;
}

.values_position {
    margin: 5px;
    display: flex;
    justify-content: space-around;
    width: 100%;
    font-size: 20px;
    font-weight: bold;
}

p {
    margin: 0;
}

.input_value {
    text-align: center;
    max-width: 80px;
    border: 2px solid aquamarine;
    border-radius: 5px;
    outline: none;
    font-weight: bold;
}

.incorrect_value {
    border-color: red;
    background-color: #ffb0b0;
}